<template>
  <div>
    <v-card>
      <v-table>
        <tbody>
          <!--   -->
          <template v-for="(comment, index) in comments" :key="index">
            <!--  -->
            <CommentDetail
              :comment="comment"
              @reply="submitReply"
              @modify="modifyComment"
              @delete="deleteComment"
            ></CommentDetail>
          </template>
          <!--    -->
          <tr>
            <td v-if="isAuthenticated" colspan="100%">
              <div class="form-area">
                <CommentForm
                  :name="member.name"
                  placehold=" "
                  v-on:submit="submitComment"
                ></CommentForm>
              </div>
            </td>
            <td v-else>
              <div class="d-flex" @click="moveToLogin">
                <div>   </div>
                <v-icon icon="mdi-chevron-right"></v-icon>
              </div>
            </td>
          </tr>
        </tbody>
      </v-table>
    </v-card>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { inject } from "vue";
import CommentForm from "@/components/form/CommentForm.vue";
import { useRouter } from "vue-router";
import CommentDetail from "@/components/detail/CommentDetail.vue";

export default {
  name: "CommentList",
  components: { CommentDetail, CommentForm },
  props: {
    postId: {
      type: Number,
      required: true,
    },
    comments: {
      type: Array,
      required: false,
      default: () => {
        return [];
      },
    },
  },
  setup(props, { emit }) {
    const store = useStore();
    const isAuthenticated = store.getters["authStore/isAuthenticated"];
    const member = store.getters["authStore/getMember"];

    const axios = inject("axios");

    let isProcessing = false;
    const startProcessing = () => {
      isProcessing = true;
    };
    const endProcessing = () => {
      isProcessing = false;
    };

    const submitComment = content => {
      if (isProcessing) {
        return;
      }
      startProcessing();
      const form = new FormData();
      form.set("content", content);
      axios
        .post(`/posts/${props.postId}/comments`, form)
        .then(() => {
          emit("reloadComments");
        })
        .catch(e => {
          if (
            e.response.status === 400 &&
            e.response.data &&
            e.response.data.message
          ) {
            alert(e.response.data.message);
          } else {
            alert("  .    .");
          }
        })
        .finally(() => {
          endProcessing();
        });
    };

    const submitReply = (parentId, content) => {
      if (isProcessing) {
        return;
      }
      startProcessing();
      const form = new FormData();
      form.set("content", content);
      axios
        .post(`/posts/${props.postId}/comments/${parentId}/replies`, form)
        .then(res => {
          console.log(res.data);
          emit("reloadComments");
        })
        .catch(e => {
          if (
            e.response.status === 400 &&
            e.response.data &&
            e.response.data.message
          ) {
            alert(e.response.data.message);
          } else {
            alert("  .    .");
          }
        })
        .finally(() => {
          endProcessing();
        });
    };

    const modifyComment = (id, content) => {
      if (isProcessing) {
        return;
      }
      startProcessing();
      const form = new FormData();
      form.set("content", content);
      axios
        .patch(`/comments/${id}`, form)
        .then(res => {
          console.log(res.data);
          emit("reloadComments");
        })
        .catch(e => {
          console.log(e.data);
        })
        .finally(() => {
          endProcessing();
        });
    };

    const deleteComment = id => {
      if (isProcessing) {
        return;
      }
      startProcessing();
      axios
        .delete(`/comments/${id}`)
        .then(res => {
          console.log(res.data);
          emit("reloadComments");
        })
        .catch(e => {
          console.log(e.data);
        })
        .finally(() => {
          endProcessing();
        });
    };

    let router = useRouter();
    const moveToLogin = () => {
      router.push({
        path: `/login`,
      });
    };

    return {
      member,
      moveToLogin,
      submitComment,
      submitReply,
      modifyComment,
      deleteComment,
      isAuthenticated,
    };
  },
};
</script>

<style scoped>
.form-area {
  padding: 15px;
}
</style>
